const urlParams = new URLSearchParams(window.location.search);
export function getQueryParameter(param) {
    return urlParams.get(param);
}
export function getQueryHash() {
    if (window.location.hash.startsWith('#opi')) {
        let itemID = window.location.hash.replace('#opi', '');
        if (itemID.length > 0) {
            return itemID;
        }
    }
    return null;
}
