import * as React from 'react';
import { default as bemCssModules } from 'bem-css-modules';
import ArrowRightSubmit from '@ori-ui/icons/lib/ArrowRightSubmit';
import { logToConsole } from '../../helpers/debugHelper';
import { SliderDirection } from '../../helpers/olapic';
import { default as OSliderButtonsModuleCss } from './OSliderButtons.module.css';
const block = bemCssModules(OSliderButtonsModuleCss);
export const OSliderButtons = ({ slide, maxSlideRange }) => {
    logToConsole({ text: 'OSliderButtons - RENDER / RECONCILE' });
    const [position, setPosition] = React.useState(0);
    const [isFirst, setIsFirst] = React.useState(true);
    const [isLast, setIsLast] = React.useState(false);
    const buttonStylesLeft = React.useMemo(() => {
        if (isFirst) {
            return block('hidden');
        }
        return `${block()} ${block('left')}`;
    }, [isFirst]);
    const buttonStylesRight = React.useMemo(() => {
        if (isLast) {
            return block('hidden');
        }
        return `${block()} ${block('right')}`;
    }, [isLast]);
    function handleClick(direction) {
        return () => {
            logToConsole({ text: 'OSliderButtons - slide', variable: direction });
            slide(direction);
            let whereAmI = position;
            if (direction === SliderDirection.left) {
                whereAmI = position - 1;
            }
            else {
                whereAmI = position + 1;
            }
            if (whereAmI > maxSlideRange) {
                whereAmI = maxSlideRange;
            }
            if (whereAmI < 0) {
                whereAmI = 0;
            }
            setIsFirst(whereAmI === 0);
            setIsLast(whereAmI === maxSlideRange);
            setPosition(whereAmI);
        };
    }
    return (React.createElement(React.Fragment, null,
        React.createElement("div", { className: buttonStylesLeft, onClick: handleClick(SliderDirection.left) },
            React.createElement(ArrowRightSubmit, { className: `${block('arrow')} ${block(`arrow-left`)}` })),
        React.createElement("div", { className: buttonStylesRight, onClick: handleClick(SliderDirection.right) },
            React.createElement(ArrowRightSubmit, { className: block('arrow') }))));
};
