/* eslint-disable react/jsx-no-useless-fragment */
import * as React from 'react';
export const EmphasizeText = ({ content, startWith }) => {
    const found = startWith.find((x) => content.includes(x));
    if (!found) {
        return React.createElement(React.Fragment, null, content);
    }
    const contentArray = content.split(' ');
    return (React.createElement(React.Fragment, null, contentArray.map((word, index) => {
        let key = `${word}${index}`;
        if (startWith.find((letter) => word.startsWith(letter))) {
            return React.createElement("strong", { key: key },
                word,
                " ");
        }
        return React.createElement(React.Fragment, { key: key },
            word,
            " ");
    })));
};
