/* eslint-disable @typescript-eslint/no-unnecessary-condition */
import * as React from 'react';
import { default as bemCssModules } from 'bem-css-modules';
import { MediaItem } from '../MediaItem/MediaItem';
import { OSliderButtons } from './OSliderButtons';
import { MediaItemPosition } from '../../helpers/olapic';
import { logToConsole } from '../../helpers/debugHelper';
import { default as OSliderModuleCss } from './OSlider.module.css';
const block = bemCssModules(OSliderModuleCss);
const itemsPerSet = 5;
const specialSet = 3;
const slideRange = 850;
export const OSlider = ({ olapicData, showDetails }) => {
    logToConsole({ text: 'Slider - RENDER / RECONCILE' });
    const totalItems = olapicData.mediaItems.length;
    const sliderRef = React.useRef(null);
    const isShort = totalItems <= itemsPerSet;
    let elementsStyles = block('elements');
    if (isShort) {
        elementsStyles = `${block('elements')} ${block('elements-mini')}`;
    }
    function slide(direction) {
        const sliderContainer = sliderRef.current;
        if (sliderContainer) {
            if (direction === 'left') {
                sliderContainer.scrollLeft -= slideRange;
            }
            else {
                sliderContainer.scrollLeft += slideRange;
            }
        }
    }
    function calculatePosition(i) {
        if (totalItems <= specialSet) {
            return MediaItemPosition.slider3;
        }
        return (i % itemsPerSet) + 1;
    }
    return (React.createElement("div", { className: block() },
        React.createElement("div", { className: elementsStyles, ref: sliderRef, dir: "ltr" },
            olapicData.mediaItems.map((item, index) => (React.createElement(MediaItem, { key: `${item.id}`, item: item, position: calculatePosition(index), showDetails: showDetails }))),
            !isShort && React.createElement("div", { className: block('elements-empty') }, "\u00A0")),
        !isShort && (React.createElement(OSliderButtons, { slide: slide, maxSlideRange: totalItems / itemsPerSet - 1 }))));
};
