import * as React from 'react';
import { default as bemCssModules } from 'bem-css-modules';
import { logToConsole } from '../../helpers/debugHelper';
import { useTranslationByKey } from '../../hooks/useTranslationsByKey';
import { default as LoadMoreModuleCss } from './LoadMore.module.css';
import { CircularProgress, Button } from '@ori-ui/material-ui';
const block = bemCssModules(LoadMoreModuleCss);
export const LoadMore = ({ handleClick, isLoading }) => {
    logToConsole({ text: 'LoadMore - RENDER / RECONCILE' });
    const t = useTranslationByKey();
    return (React.createElement("div", { className: block() },
        React.createElement(Button, { variant: "outlined", className: block('button'), disabled: isLoading, onClick: handleClick }, t('Load more')),
        isLoading && (React.createElement(CircularProgress, { size: 24, className: block('button-progress') }))));
};
