import ReactDOM from 'react-dom';
import React from 'react';
import { App } from './App';
export const render = (rootEl) => {
    if (!rootEl.dataset.config) {
        // if there is no config in the dataset then we will not even
        // try to do anything else
        return;
    }
    const configData = JSON.parse(rootEl.dataset.config);
    const dataSet = Object.assign({}, configData);
    const { apiKey = process.env.REACT_APP_API_KEY, isGallery = false, isProduct = false, tag = '', } = dataSet;
    if (!apiKey || apiKey.length < 1) {
        // if there is no apiKey or it is empty we will not even
        // try to do anything else
        return;
    }
    let baseURL = process.env.REACT_APP_BASE_URL;
    if (process.env.NODE_ENV === 'production') {
        if (typeof window === 'undefined') {
            baseURL = '';
        }
        else {
            baseURL = window.location.origin;
        }
    }
    const config = {
        apiKey,
        baseURL,
        categoryId: '',
        customerId: '',
        isGallery,
        isHome: !tag,
        isProduct,
        language: '',
        streamId: '',
        tag,
    };
    ReactDOM.render(React.createElement(App, { config: config }), rootEl);
};
